<template>

 


  <div class="container"> 
  <header>
    <h3>Yemekçiler</h3>
  </header>

    <div class="table-wrapper">
      <table class="table-users">
        <tr>
          <th style="width: 50%;">Yemekçi İsmi</th>
          <th style="width: 30%;">Grup</th>
          <th style="width: 20%;">İşlem</th>
        </tr>
                            <tr v-for="yemekci in YemekcilerDizi" :key="yemekci.key">
                        <td>{{ yemekci.yemekci_isim }}</td>
                        <td>{{ yemekci.yemekci_grup }}</td>
                        <td>
                            <router-link :to="{name: 'yemekciedit', params: { id: yemekci.key }}" class="btn btn-edit">
                              <i class="material-icons">create</i>
                            </router-link>
                            <button @click.prevent="deleteYemekci(yemekci.key)"  class="btn btn-delete">
                              <i class="material-icons">remove_circle_outline</i>
                              </button>
                        </td>
                    </tr>
      </table>
       <router-link to="/yemekciadd"><button class="btn btn-add">Yemekçi Ekle</button></router-link>
    </div>

   

  </div>

</template>

<script>
import { db } from "../firebaseDb";

export default {
  data() {
    return {
      YemekcilerDizi: [],
    };
  },
  created() {
   
db.collection("yemekciler").orderBy("yemekci_grup", "asc").onSnapshot((snapshotChange) => {
      this.YemekcilerDizi = [];
      snapshotChange.forEach((doc) => {
        this.YemekcilerDizi.push({
          key: doc.id,
          yemekci_isim: doc.data().yemekci_isim,
          yemekci_grup: doc.data().yemekci_grup,
        });
      });
    });
  },
  methods: {
    deleteYemekci(id) {
      if (window.confirm("Silmek İstiyor musunuz?")) {
        db.collection("yemekciler")
          .doc(id)
          .delete()
          .then(() => {
            console.log("Document deleted!");
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
  },
};
</script>

